import {Component} from '@angular/core';

import {IonicPage, NavParams} from 'ionic-angular';
import {CallNumber} from "@ionic-native/call-number";

@IonicPage()
@Component({
  selector: 'celldetail-page',
  templateUrl: 'cell-detail.html'
})
export class  CellGroupDetailPage
{
cellgroup:any;
  private callNumber: any;

  constructor(
    navParams: NavParams,
    callNumber: CallNumber) {
  this.cellgroup = navParams.get('data') || "None";
  console.log(this.cellgroup.title)

  }

  callL(){
    this.callNumber.callNumber(this.cellgroup.contact_number , true)

      .then(res => console.log('Launched dialer!', res))
      .catch(err => console.log('Error launching dialer', err));
  }
}



